/* 
  navigation bar styling
  including media query
*/
nav {
  position: fixed;
  display: grid;
  align-items: center;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 64px;
  padding: 7px 0;
  border-top: 1px solid var(--font-color);
  background-color: white;
}
nav > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 14px;
  grid-template-columns: 1fr repeat(4, 48px) 1fr;
  align-items: center;
  justify-items: center;
}
nav > ul > li {
  height: 40px;
  width: 48px;
  display: grid;
  grid-template-rows: 28px 12px;
  justify-items: center;
  align-items: baseline;
  font-size: 10px;
  line-height: 12px;
}
nav > ul > li > img {
  height: 28px;
  width: 28px;
  transition: 0.3s;
}
nav > ul > li.active > label {
  text-decoration: underline;
}
@media (min-width: 1100px) {
  nav {
    width: 64px;
    height: 100vh;
    top: 0;
    align-items: baseline;
    border-top: 0;
    border-right: 1px solid var(--font-color);
  }
  nav > ul {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 48px) 1fr;
  }
}