/*
  import font
*/
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap');
/*
  Declare variables for font color and text sizes
*/
:root {
  --font-color: #030303;
  --error-color: red;
  --h1-font-size: clamp(22px, 4.8vmin, 32px);
  --h2-font-size: clamp(19px, 4.4vmin, 26px);
  --h3-font-size: clamp(17px, 3.8vmin, 22px);
  --body-text-size: clamp(14px, 2.8vmin, 16px);
}
/*
  reset
*/
body {
  margin: 0;
  padding: 0;
}
/* 
  default main styling
*/
main {
  width: min(84vw, 900px);
  margin: 0 auto 98px auto;
}
main > div {
  margin: var(--body-text-size) 0;
}
/*
  general image styling
*/
img {
  width: 100%;
}
/* 
  button styling
*/
button {
  font-family: 'Montserrat', sans-serif;
  background-color: #f7f7f7;
  opacity: 0.9;
  border: 1px solid #666;
  font-weight: 200;
  padding: 5px 12px;
  height: calc(var(--body-text-size) * 1.5 + 10);
  border-radius: var(--body-text-size);
  max-width: 15ch;
}
button:hover {
  opacity: 1;
  filter: blur(0.1px);
}
button:first-of-type:not(:last-of-type) {
  margin-right: 0;
  border-right: 1px solid #333;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
button + button {
  margin-left: 0;
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.fake-button {
  text-decoration: underline;
  text-align: center;
  cursor: pointer;
}
/*
  helpers
*/
.grid {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr;
}
.grid > * {
  width: 100%;
}
.none {
  display: none;
}
/* loading animation - screen freeze */
#loading-animation {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: #666;
  opacity: 0.4;
}
/* 
  dont show attributions by default
*/
#attributions {
  display: none;
}
/* 
  registration/login page styling
*/
.phone-number-input {
  display: grid;
  align-items: center;
  justify-items: center;
}