@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap);
/*
  import font
*/
/*
  Declare variables for font color and text sizes
*/
:root {
  --font-color: #030303;
  --error-color: red;
  --h1-font-size: clamp(22px, 4.8vmin, 32px);
  --h2-font-size: clamp(19px, 4.4vmin, 26px);
  --h3-font-size: clamp(17px, 3.8vmin, 22px);
  --body-text-size: clamp(14px, 2.8vmin, 16px);
}
/*
  reset
*/
body {
  margin: 0;
  padding: 0;
}
/* 
  default main styling
*/
main {
  width: min(84vw, 900px);
  margin: 0 auto 98px auto;
}
main > div {
  margin: clamp(14px, 2.8vmin, 16px) 0;
  margin: var(--body-text-size) 0;
}
/*
  general image styling
*/
img {
  width: 100%;
}
/* 
  button styling
*/
button {
  font-family: 'Montserrat', sans-serif;
  background-color: #f7f7f7;
  opacity: 0.9;
  border: 1px solid #666;
  font-weight: 200;
  padding: 5px 12px;
  height: calc(clamp(14px, 2.8vmin, 16px) * 1.5 + 10);
  height: calc(var(--body-text-size) * 1.5 + 10);
  border-radius: clamp(14px, 2.8vmin, 16px);
  border-radius: var(--body-text-size);
  max-width: 15ch;
}
button:hover {
  opacity: 1;
  -webkit-filter: blur(0.1px);
          filter: blur(0.1px);
}
button:first-of-type:not(:last-of-type) {
  margin-right: 0;
  border-right: 1px solid #333;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
button + button {
  margin-left: 0;
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.fake-button {
  text-decoration: underline;
  text-align: center;
  cursor: pointer;
}
/*
  helpers
*/
.grid {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr;
}
.grid > * {
  width: 100%;
}
.none {
  display: none;
}
/* loading animation - screen freeze */
#loading-animation {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: #666;
  opacity: 0.4;
}
/* 
  dont show attributions by default
*/
#attributions {
  display: none;
}
/* 
  registration/login page styling
*/
.phone-number-input {
  display: grid;
  align-items: center;
  justify-items: center;
}
/*
  import font
*/
/*
  global typography settings
*/
* {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  color: var(--font-color);
  font-weight: 200;
}
/* 
  general header and heading style
*/
header {
  width: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
  text-align: center;
}
/* 
  Concept:
    h[1-3] are desktop
    h[4-6] are mobile
    Same font-size and color, but uses media query to change display
*/
h1, h2, h3, h4, h5, h6 {
  display: none;
}
.dont-hide {
  display: unset;
}
@media (min-width: 1100px) {
  h1 {
    display: inline-block;
    text-align: center;
    font-size: var(--h1-font-size);
    font-weight: 300;
    text-decoration: underline;
  }
  h2 {
    display: inline-block;
    text-align: center;
    font-size: var(--h2-font-size);
  }
  h3 {
    display: inline-block;
    text-align: center;
    font-size: var(--h3-font-size);
  }
}
@media (max-width: 1099px) {
  h4 {
    display: inline-block;
    text-align: center;
    font-size: var(--h1-font-size);
    font-weight: 300;
    text-decoration: underline;
  }
  h5 {
    display: inline-block;
    text-align: center;
    font-size: var(--h2-font-size);
  }
  h6 {
    display: inline-block;
    text-align: center;
    font-size: var(--h3-font-size);
  }
}
b {
  font-weight: 500;
}
.error {
  color: var(--error-color);
  text-align: center;
  text-decoration: underline;
  margin: var(--h1-font-size);
}
/* 
  navigation bar styling
  including media query
*/
nav {
  position: fixed;
  display: grid;
  align-items: center;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 64px;
  padding: 7px 0;
  border-top: 1px solid var(--font-color);
  background-color: white;
}
nav > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-gap: 14px;
  gap: 14px;
  grid-template-columns: 1fr repeat(4, 48px) 1fr;
  align-items: center;
  justify-items: center;
}
nav > ul > li {
  height: 40px;
  width: 48px;
  display: grid;
  grid-template-rows: 28px 12px;
  justify-items: center;
  align-items: baseline;
  font-size: 10px;
  line-height: 12px;
}
nav > ul > li > img {
  height: 28px;
  width: 28px;
  transition: 0.3s;
}
nav > ul > li.active > label {
  text-decoration: underline;
}
@media (min-width: 1100px) {
  nav {
    width: 64px;
    height: 100vh;
    top: 0;
    align-items: baseline;
    border-top: 0;
    border-right: 1px solid var(--font-color);
  }
  nav > ul {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 48px) 1fr;
  }
}

main.settings div {
  margin: 0;
  display: grid;
  align-items: center;
  justify-items: center;
  text-align: center;
  overflow: hidden;
  width: 100%;
}
main.settings > div {
  padding: 32px 0;
}
main.settings > div:not(:last-of-type) {
  border-bottom: 1px solid #ccc;
}
main.settings > div > *:last-child, main.settings > div > *:first-child {
  margin: 0;
}
main.settings > div > *:not(:first-child):not(:last-child) {
  margin: 7px;
}
main.settings > div.action > * {
  text-decoration: underline;
  cursor: pointer;
}
.setting {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.setting > *:first-child {
  text-align: left;
}
.setting > *:nth-child(2) {
  text-align: right;
}
.setting-group > h3 {
  margin: var(--h1-font-size);
  text-decoration: underline;
}
.setting-group > .setting {
  margin: 18px;
}
.toggle {
  display: inline-block;
  width: 48px;
  height: 24px;
  border-radius: 12px;
  border: 2px solid #666;
  transition: 0.5s;
}
.toggle::after {
  content: "";
  width: 14px;
  height: 14px;
  border-radius: 7px;
  background-color: #666;
  position: relative;
  top: 2px;
  display: inline-block;
  left: -26px;
  transition: 0.2s;
}
.toggle.on {
  border-color: #787;
}
.toggle.on::after {
  background-color: #787;
  left: -2px;
}
/*
  writing - question-answer styling
*/
.freeform {
  padding: 4px 6px;
}
.questions {
  margin: 0 2vw;
}
.questions > div {
  margin-top: 7px;
  padding: 3px 5px;
  text-align: left;
}
.questions > div::before {
  content: "";
  outline: none;
  position: absolute;
  margin-left: -24px;
  margin-top: -25px;
  width: var(--body-text-size);
  height: var(--body-text-size);
  background-size: cover;
  background-image: url(data:image/png;base64,);
}
.questions:hover > div::before {
  outline: none;
}
.questions > div:not(:last-child) {
  margin-bottom: 24px;
}
/* Add Question Dialog styling */
.current-question-list {
  list-style-type: none;
  margin: var(--h1-font-size);
  padding: 0;

}
.current-question-list > li {
  padding: 5px 12px;
}
.add-question-input {
  padding: 5px 12px;
}
/* 
  journal styling
*/
.entry-header {
  grid-template-columns: 1fr 4fr 1fr;
}
section:not(:last-of-type) {
  margin-bottom: 24px;
}
section > ul {
  font-weight: 200;
  font-size: var(--body-text-size);
  list-style-type: none;
  margin: 0;
  padding: 0;
}
section > ul > li {
  line-height: 21px;
  letter-spacing: 0.5px;
}
.pill {
  display: grid;
  justify-items: right;
}
.pill > span {
  text-align: center;
  width: 3ch;
  border: 1px solid #ccc;
  background-color: #fafafa;
  border-radius: 6px;
}

